/* ============================================= */
/*              Navigation                       */
/* ============================================= */
@import '../utilities/mixin';

//* New Code *//
.nav {
  padding-left: 0;
  flex-basis: 100%;
  display: flex;
  flex-direction: column;

  @include media-querie ('md') {
    flex-direction: row;
    justify-content: flex-end;
    align-items: center;
  }
}

.nav-link {
  text-align: center;
  border-radius: 5px;
  padding: 10px 0;
  display: block;
  width: 100%;
  transition: 
    background-color .2s ease-in-out, 
    color .2s ease-in-out;

  @include media-querie ('md') {
      flex-basis: 100px;
  }
}

.nav-link:hover {
  color: $color-white;
  background-color: $color-primary;
}
